.calendar-table {
  width: 100%;
  height: 40vh;
  background: #fafafa;
  display: grid;
  grid-template-rows: repeat(6, auto);
  grid-template-columns: repeat(7, auto);
}

.button {
  cursor: pointer;
  border: 0;
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background: #fafafa;
}

.calendar-table-head {
  display: grid;
  grid-template-columns: repeat(7, auto);
}

.date {
  padding: 8px 16px;
  margin-bottom: 4px;
}

.selected {
  background: tomato;
}

.start {
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}

.end {
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

.solo {
  border-radius: 20px;
}
